@import 'productParameters.css';

.product {
  display: flex;
  flex-direction: column;
  background: #d8d8d8;
  padding: 10px;
  margin-bottom: 15px;
  border-radius: 10px;
}

.product--error {
  border: 2px solid red;
}

.product__close-wrapper {
  text-align: right;
  font-size: 2em;
}

.product__close::before {
  cursor: pointer;
}

.product .name-input {
  width: 350px;
  max-width: 95%;
  background-color: #e6e6e6;
  color: black;
}

.product__type {
  margin-top: 10px;
}

.product__inputs > label {
  display: block;
  margin-bottom: 15px;
}

.product input,
.product textarea {
  width: 150px;
  padding: 5px;
  border-radius: 4px;
  border: 1px solid black;
}

.product textarea {
  vertical-align: top;
}

.product__buttons-wrapper {
  margin-bottom: 10px;
}

.product input[type='number'] {
  width: 90px;
}
